<template>
  <div class="main">
    <van-nav-bar :title="'影院'" id="navBar" @click-left="clickCity" @click-right="searchCinema">
      <template #left>
        {{ $store.state.cityName }}
        <van-icon name="arrow-down" color="black" />
      </template>
      <template #right>
        <van-icon name="search" size="22" color="black" />
      </template>
    </van-nav-bar>
    <CinemaList :height=getCinemaListHeight()></CinemaList>
  </div>
</template>
<script>
import CinemaList from "@/components/CinemaList";
import { NavBar } from 'vant';
import { Icon } from 'vant';
export default {
  components: {
    CinemaList,
    [NavBar.name]: NavBar,
    [Icon.name]: Icon,
  },
  methods: {
    getCinemaListHeight() {
      const html = document.documentElement
      const htmlSize = parseInt(getComputedStyle(html).fontSize)
      return window.innerHeight - 3.0625 * htmlSize - 2.875 * htmlSize + 'px'
    },
    clickCity() {
      this.$router.push({
        path: "/city",
      });
    },
    searchCinema() {
      this.$router.push({
        path: "/cinemas/search",
      });
    },
  },

};
</script>
